body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
  color: white;
}

input {
  background-color: black;
  color: white;
}

i:hover {
  color: red;
}

.w3-row-padding img {
  margin-bottom: 12px;
}
/* Set the width of the sidebar to 120px */
.w3-sidebar {
  width: 120px;
  background: #222;
}
/* Add a left margin to the "page content" that matches the width of the sidebar (120px) */
#main {
  margin-left: 120px;
}
/* Remove margins from "page content" on small screens */
@media only screen and (max-width: 600px) {
  #main {
    margin-left: 0;
  }
  .bio {
    font-size: 12px !important;
  }
  #GA-logo {
    height: 8vh;
  }
  .Skills-logos {
    width: 10vw !important;
    height: 4vh !important;
  }
  #python {
    width: 8vw !important;
    height: 3vh !important;
  }
  #react {
    width: 10vw !important;
  }
  #vscode {
    width: 8vw !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .Skills-logos {
    width: 4vw !important;
    height: 4vh !important;
  }
  #python {
    margin-left: -4px !important;
  }
  #passportjs {
    height: 3.95vh !important;
  }
  #vscode {
    width: 4vw !important;
    height: 3.6vh !important;
    margin-left: -6px !important;
  }
  #react {
    width: 5.5vw !important;
    margin-left: -6.5px !important;
  }
  #nodejs {
    margin-right: 4px !important;
    margin-left: 0px !important;
    margin-top: 1px !important;
    height: 3.65vh !important;
  }
  #AWS {
    height: 4vh !important;
  }
  #heroku {
    height: 4.05vh !important;
  }
  #bootstrap {
    border-radius: 12px !important;
  }
  #vscode {
    height: 3.7vh !important;
  }
  #OAuth {
    height: 4.05vh !important;
  }
  #GA-logo {
    height: 7vh;
  }
}

@media (max-width: 600px) {
  .hide-li {
    display: none;
  }
  .mobile-bord-bot {
    border-bottom: 1px solid rgba(117, 117, 117, 1) !important;
  }
}

.add-bord-bot {
  border-bottom: 1px solid rgba(117, 117, 117, 1) !important;
}

.add-bord-top {
  border-top: 1px solid rgba(117, 117, 117, 1) !important;
  padding-top: 10px;
}

.gray-bord-bot {
  border-bottom: 1px solid rgba(117, 117, 117, 1) !important;
}

.gray-bord-bot:last-child {
  border: 1px solid transparent !important;
}

.project-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 720px;
  opacity: 0.75;
  transition: opacity 0.3s ease;
  background: rgba(10, 10, 10, 1);
  margin: 0 0 20px;
  border: 2px solid rgb(100, 11, 11);
  border-radius: 10px;
}

.project-card:hover {
  opacity: 1;
}

.project-link-ctnr {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding: 10px 0;
}

.footer-link-ctnr {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.project-img {
  max-width: 90%;
  max-height: 400px;
}

.mobile-nav-btn {
  padding: 5px 10px;
  float: left;
  width: auto;
  border: none;
  display: block;
  outline: 0;
}

.project-description {
  width: 90%;
  color: white;
  font-size: 22px;
}

#HeadShot {
  border: 3px solid rgb(100, 11, 11);
  border-radius: 10px;
}

#main h1 {
  text-shadow: 1.5px -2px rgb(100, 11, 11);
}

#myNavbar {
  border: 1px solid rgb(100, 11, 11);
}

#network-links {
  border: 2px solid rgb(100, 11, 11);
  border-radius: 10px;
}

#submit-btn {
  border: 2px solid rgb(100, 11, 11);
  border-radius: 5px;
}

#GA-logo {
  width: 5vw;
  margin: 0 0 -10px 0;
}

.bio {
  font-size: 20px;
}

.Skills-logos {
  width: 2vw;
  height: 3vh;
  margin: -4px 5px 0 0;
}

#nodejs {
  margin-left: 5px;
  margin-right: 0px;
}

#react {
  margin-left: 3px;
  margin-right: 0px;
}

#python {
  width: 1.5vw;
  height: 3vh;
  margin-left: 3px;
  margin-right: 6px;
}

#vscode {
  width: 1.5vw;
  height: 2.5vh;
  margin-right: 10px;
}

#materialize {
  margin-left: -2.5px;
}

#bootstrap {
  border-radius: 5px;
  margin-left: -1px;
}

#passportjs {
  background-color: rgba(189, 188, 188, 0.308);
  border-radius: 5px;
}

#AWS {
  background-color: rgba(255, 255, 255, 0.623);
  border-radius: 5px;
}

#github {
  background-color: rgb(172, 171, 171);
  border-radius: 5px;
}

#command-line {
  background-color: rgb(172, 171, 171);
  border-radius: 5px;
}

#trello {
  background-color: rgb(172, 171, 171);
  border-radius: 5px;
}

#heroku {
  border-radius: 5px;
}
